.cryptos{
  width:100%;
  margin-top:10px;
  background-color: inherit;
  padding:10px;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  gap:10px;

    .coinName{
      background-color: inherit;
      display: flex;
      flex-direction:row;
      justify-content: space-between;

      .coinGroup{
        background-color: inherit;
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        gap:10px;
        
        .img{
          background-color: inherit;
          width:20px;
          
          img{
            background-color: inherit;
            width: 100%;
          }
        }

        .currencyName{
          background-color: inherit;
        }

      }
      
      .dollar{
        background-color: inherit;
        p{
          font-size: 14px;
          background-color: inherit;
        }
      }
    }

    .coinName{
      p{
        background-color: inherit;
      }
      .value{
        background-color: inherit;
        p{
          color: #0ecb81;
          background-color: inherit;
        }
      }
    }
  
  &:hover{
    background-color: var(--faq-no-border-color);
   // background-color: red;
  }  
}